@import '../variables.scss';
@import '../mixins.scss';

.questions {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgba(50, 103, 153, 1);

  &__wrap {
    max-width: $desktop-width;
    width: 100%;
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
      rgba(162, 162, 162, 0.6);
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
      0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
      0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
      0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
      0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);

    text-transform: uppercase;
  }

  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;

    span:first-child {
      margin-right: 10px;
    }
  }

  &__list {
    @include list-reset;
    font-weight: 400;
    font-size: 20px;
    line-height: 26px;
    padding-left: 40px;
    margin: 40px 0;
  }

  &__item {
    margin: 20px 0;
  }

  &__label {
    position: relative;
  }

  &__label::after {
    position: absolute;
    content: '';
    top: -3px;
    left: -40px;
    width: 32px;
    height: 32px;
    background-image: url('../../img/check-off.svg');
  }

  &__input {
    position: absolute;
    z-index: -1;
    opacity: 0;
  }

  &__input:checked + &__label::after {
    background-image: url('../../img/check-on.svg');
  }

  &__buttons {
    display: flex;
    flex-direction: column;
  }

  &__btn {
    border: none;
    outline: none;
    color: #fff;
    font-size: 20px;
    text-transform: uppercase;
    padding: 12px 22px 10px;
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
      rgba(162, 162, 162, 0.6);
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
      0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
      0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
      0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
      0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
    border-radius: 101.145px;
    transition: 0.3s;
    line-height: 30px;
    margin-bottom: 10px;
  }

  &__btn:disabled {
    opacity: 0.5;
  }

  &__btn:focus,
  &__btn:hover {
    box-shadow: 0px 0px 3px 3px rgba(34, 60, 80, 0.1);
  }

  &__btn:active {
    opacity: 0.7;
  }

  @media (min-width: $tablet-width) {
    &__title {
      font-size: 26px;
      line-height: 34px;
      width: 80%;
    }

    &__list {
      font-size: 26px;
      line-height: 34px;
    }

    &__label::after {
      top: 0px;
      left: -50px;
    }
  }

  @media (min-width: $desktop-width) {
    &__wrap {
      border-radius: 75px;
      padding: 60px 0 40px;
    }

    &__list {
      align-self: flex-start;
      padding-left: 160px;
    }

    &__buttons {
      flex-direction: row;
      justify-content: space-between;
      width: 80%;
    }
  }
}
