@import '../variables.scss';
@import '../mixins.scss';

.table__wrap {
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  border-radius: 42.484px;
  padding: 27px 37px;
  color: #326799;
}

.title {
  font-size: 21.242px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  color: #ffffff;
  padding: 14px;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
    rgba(162, 162, 162, 0.6);
  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
    0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
    0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
    0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
    0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
  border-radius: 57.3534px;
  position: relative;

  &::before {
    position: absolute;
    content: '';
    width: 37px;
    height: 28px;
    top: 13px;
    left: 335px;
    background-image: url('../../img/results.svg');
    background-repeat: no-repeat;
  }
}

table {
  border: 1px solid #6c7395;
  border-collapse: collapse;
}

th {
  font-weight: normal;
  border: 1px solid #6c7395;
  padding: 7px;
}

td {
  border: 1px solid #6c7395;
  padding: 7px;
  text-align: center;
}

.list {
  @include list-reset;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;

  background: #ffffff;
  border-radius: 22px;
  padding: 10px 20px;
  font-size: 16px;
  line-height: 16px;
  margin-top: 5px;
  color: #326799;
  box-shadow: 0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
    0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
}

.button {
  border: none;
  outline: none;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(176deg, #a2edcd 3.27%, rgba(255, 255, 255, 0) 208.29%);
  border-radius: 57px;
  color: #326799;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  padding: 5px 20px;
  width: 340px;
  transition: 0.3s;
  margin-bottom: 5px;

  img {
    margin-left: 10px;
  }

  span {
    width: 100%;
    text-align: center;
  }
}

.button:last-child {
  margin-bottom: 0;
}

.button:focus,
.button:hover {
  box-shadow: 0px 0px 3px 3px rgba(34, 80, 55, 0.3);
}

.button:active {
  opacity: 0.7;
}

.info {
  background: #ffffff;
  border-radius: 22px;
  padding: 30px 40px;
  font-size: 16px;
  line-height: 24px;
  margin-top: 5px;
  color: #326799;
  box-shadow: 0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
    0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
}

.questions {
  @include list-reset;
  background: #ffffff;
  border-radius: 22px;
  padding: 30px 40px;
  font-size: 16px;
  line-height: 24px;
  margin-top: 5px;
  color: #326799;
  box-shadow: 0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
    0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
  & > li {
    margin-bottom: 10px;  span {
      display: inline-block;
      margin-bottom: 5px;
    }
  }
}
