@import './normalize.scss';
@import './variables.scss';

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat Regular'),
    url('../fonts/Montserrat-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Montserrat';
  src: local('Montserrat Medium'),
    url('../fonts/Montserrat-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
  overflow-x: hidden;
  background-color: #c2f2f2;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Geneva', sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  color: #000000;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

#root {
  background-color: #a0bec8;
  background-image: url('../img/b-i-big-min.jpg');
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
  @media (min-width: $desktop-width) {
    min-height: 1080px;
  }
}

.loader {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.2);

  svg {
    position: fixed;
    top: calc(50% - 32px);
    left: calc(50% - 32px);
  }
}