@import '../variables.scss';
@import '../mixins.scss';

.title_questions {
  font-size: 21.242px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  color: #ffffff;
  padding: 14px;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
    rgba(162, 162, 162, 0.6);
  box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
    0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
    0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
    0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
    0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
  border-radius: 57.3534px;
  position: relative;

  &::before {
    position: absolute;
    content: '';
    width: 29px;
    height: 22px;
    top: 16px;
    left: 445px;
    background-image: url('../../img/question-list.svg');
    background-repeat: no-repeat;
  }
}

.questions {
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  border-radius: 42.484px;
  padding: 27px 37px;
  display: flex;
  flex-direction: column;
  align-items: center;

  textarea {
    outline: none;
    border: none;
    background: #ffffff;
    border-radius: 22px;
    padding: 20px;
    font-size: 16px;
    line-height: 16px;
    color: #326799;
    // margin-right: 18px;
    transition: 0.3s;
    width: 100%;
  }

  textarea:focus {
    box-shadow: 0 0 3px 3px rgba(47, 83, 116, 0.4);
  }

  span {
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(176deg, #a2edcd 3.27%, rgba(255, 255, 255, 0) 208.29%);
    border-radius: 57px;
    color: #326799;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 13px;
    padding: 5px 20px;
    transition: 0.3s;
    width: 230px;
    margin-bottom: 20px;
    cursor: pointer;

    img {
      margin-left: 10px;
    }
  }

  span:hover {
    box-shadow: 0px 0px 3px 3px rgba(34, 60, 80, 0.1);
  }

  span:active {
    opacity: 0.7;
  }
}
