@import '../variables.scss';
@import '../mixins.scss';

.title {
    font-size: 21.242px;
    line-height: 26px;
    text-align: center;
    text-transform: uppercase;
    font-weight: normal;
    color: #ffffff;
    padding: 14px;
    background: linear-gradient(
        0deg,
        #98ffd4 -50%,
        rgba(255, 255, 255, 0) 71.25%
      ),
      linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%),
      rgba(162, 162, 162, 0.6);
    box-shadow: 0px 100px 80px rgba(0, 0, 0, 0.07),
      0px 58.88px 41.728px rgba(0, 0, 0, 0.050624),
      0px 30.24px 19.584px rgba(0, 0, 0, 0.039312),
      0px 12.16px 8.576px rgba(0, 0, 0, 0.030688),
      0px 2.72px 3.712px rgba(0, 0, 0, 0.019376);
    border-radius: 57.3534px;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      width: 29px;
      height: 22px;
      top: 16px;
      left: 425px;
      background-image: url('../../img/user-list.svg');
      background-repeat: no-repeat;
    }
  }

.list {
  @include list-reset;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(180deg, #ffffff 0%, rgba(255, 255, 255, 0) 100%);
  border-radius: 42.484px;
  padding: 27px 37px;

  input {
    outline: none;
    border: none;
    background: #ffffff;
    border-radius: 22px;
    padding: 10px 20px;
    font-size: 16px;
    line-height: 16px;
    margin-top: 5px;
    color: #326799;
    margin-right: 18px;
    transition: 0.3s;
  }

  input:focus {
    box-shadow: 0 0 3px 3px rgba(47, 83, 116, 0.4);
  }

    textarea {
    outline: none;
    border: none;
    background: #ffffff;
    border-radius: 22px;
    padding: 20px;
    font-size: 16px;
    line-height: 16px;
    color: #326799;
    margin-right: 18px;
    transition: 0.3s;
    width: 100%;
  }

  textarea:focus {
    box-shadow: 0 0 3px 3px rgba(47, 83, 116, 0.4);
  }
}

.user {
  width: 500px;
  background-color: inherit;
}

.user:focus {
  background-color: #fff;
}

.mail {
  width: 270px;
}

.label {
  &__user {
    width: 500px;
    color: rgba(50, 103, 153, 1);
    text-align: center;
    margin-right: 18px;

    input {
      width: 500px;
    }
  }

  &__email {
    width: 270px;
    color: rgba(50, 103, 153, 1);
    text-align: center;

    input {
      width: 270px;
    }
  }

  &__textarea_wrap {
    width: 100%;
    color: rgba(50, 103, 153, 1);
    text-align: center;

    textarea {
      margin-top: 10px;
    }
  }
}

.item,
.item__new {
  display: flex;
  align-items: center;
}

.item__new {
  margin-top: 10px;
}

.item:last-child {
  margin-top: 20px;
  flex-direction: column;
  label {
    margin-bottom: 10px;
  }
}

.button {
  border: none;
  outline: none;
  background: linear-gradient(0deg, #98ffd4 -50%, rgba(255, 255, 255, 0) 71.25%),
    linear-gradient(176deg, #a2edcd 3.27%, rgba(255, 255, 255, 0) 208.29%);
  border-radius: 57px;
  color: #326799;
  display: flex;
  align-items: center;
  font-size: 13px;
  // height: 33px;
  padding: 5px 20px;
  width: 320px;
  transition: 0.3s;

  img {
    margin-left: 10px;
  }
}

.button:focus,
.button:hover {
  box-shadow: 0px 0px 3px 3px rgba(34, 80, 55, 0.3);
}

.button:active {
  opacity: 0.7;
}
