@import '../variables.scss';

.login {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(1, 20, 47, 0.65);
  font-size: 16px;

  &__wrap {
    background-color: #fff;
    display: flex;
    flex-direction: column;
    padding: 20px;

    @media (min-width: $tablet-width) {
      padding: 20px 60px;
    }
  }

  &__form_wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  label {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
  }

  input {
    margin-top: 5px;
    padding: 10px;
    @media (min-width: $tablet-width) {
      width: 300px;
    }
  }

  &__button {
    width: 150px;
    padding: 9px;
    background-color: #0d3b4c;
    color: #fff;
    border: none;
    font-size: 16px;
    margin-top: 10px;
    margin-bottom: 20px;

    &:focus {
      outline: none;
    }
  }
}
